<template>
  <div>
    <Header/>
    <el-container class="main-container">
      <el-main>
        <div class="token-detail-content">
          <div class="con-title">
            {{ $t('lang.token_detail_title') }}
          </div>
          <el-row>
            <el-col><label for="">{{ $t('lang.token_detail_table1') }}</label>
              <p>{{ transData.name }}</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table2') }}</label>
              <p>
                <router-link tag="a" :to="{path:'/tx',query:{txid: transData.txhash }}">{{ transData.txhash }}
                </router-link>
                <img src="@/assets/img/copy.png" alt="" @click="copy(transData.txhash)">
                </p>
            </el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table3') }}</label>
              <p v-if="transData.status === 'Success'" style="color:rgba(226,138,4,1);">{{ transData.status }}</p>
              <p v-else>{{ transData.status }}</p>
            </el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table4') }}</label>
              <p>{{ transData.block }}</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table5') }}</label>
              <p>{{ filterDate(transData.timestamp) }}</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table6') }}</label>
              <p>
                <router-link tag="a" :to="{path:'/address',query:{address: transData.from_gkc }}">{{ transData.from_gkc }}
                </router-link>
                <img v-if="transData.from_gkc !== ''" src="@/assets/img/copy.png" alt="" @click="copy(transData.from_gkc)">
              </p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table7') }}</label>
              <p> <router-link tag="a" :to="{path:'/address',query:{address: transData.to_gkc }}">{{ transData.to_gkc }}
              </router-link>
                <img src="@/assets/img/copy.png" alt="" @click="copy(transData.to_gkc)">
              </p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table8') }}</label>
              <p>{{ transData.value }} {{ transData.symbol }}</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table9') }}</label>
              <p>{{ transData.amount }} GKC</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table10') }}</label>
              <p>{{ transData.fee }} GKC</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table11') }}</label>
              <p>{{ transData.gaslimit }}</p></el-col>
            <el-col><label for="">{{ $t('lang.token_detail_table12') }}</label>
              <p>{{ transData.gasprice }}</p></el-col>
          </el-row>
        </div>
      </el-main>
    </el-container>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/Header'
  import Footer from '@/components/Footer'
  import QRCode from 'qrcodejs2'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
    },
    data() {
      return {
        transData: [],
        txid: ""
      }
    },
    created() {
      this.txid = this.$route.query.txid;
    },
    watch: {},
    computed: {},
    mounted() {
      this.getTokenInfoByHash()
    },
    methods: {
      getTokenInfoByHash() {
        this.$http.post("api/contract/detail", {txid: this.txid}).then(res => {
          if (res.data.code === 200) {
            this.transData = res.data.data.data
          }
        })
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      copy(data){
        let url = data;
        let oInput = document.createElement('input');
        oInput.value = url;
        document.body.appendChild(oInput);
        oInput.select();
        document.execCommand("Copy");
        this.$message.success({
          message: 'Replication success!',
          type: 'success'
        });
        oInput.remove()
      },
    }
  }


</script>
<style>
  .el-main {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;
    flex-direction: column;
    align-items: center;
  }

  .el-main .token-detail-content .con-title {
    height: 60px;
    background: rgba(243, 244, 247, 1);
    border-radius: 4px 4px 0 0;
    font-size: 16px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    line-height: 18px;
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 0 27px;
  }

  .el-main .token-detail-content {
    width: 1200px;
    min-width: 1200px;
  }

  .el-main .token-detail-content .el-row {
    background: rgba(255, 255, 255, 1);
    border-radius: 4px;
    padding: 0 26px;
    display: flex;
    align-items: center;
    flex-direction: column;

  }

  .el-main .token-detail-content .el-row .el-col {
    display: flex;
    justify-content: start;
    align-items: center;
    height: 60px;
    border-bottom: 1px solid rgba(240, 240, 240, 1);
  }

  .el-main .token-detail-content .el-col:last-child {
    border-left: none;
  }

  .el-main .token-detail-content .el-row .el-col label {
    width: 26%;
    font-size: 20px;
    font-weight: 400;
    color: rgba(0, 0, 0, 1);
    height: 35px;
    line-height: 35px;
    text-align: left;
  }

  .el-main .token-detail-content .el-row .el-col p {
    width: 74%;
    font-weight: 200;
    height: 35px;
    line-height: 35px;
    text-align: left;
    font-size:16px;
    color:rgba(0,0,0,1);
    opacity:1;
  }
  .el-main .token-detail-content .el-row .el-col p a {
    font-size:16px;
    color:rgba(0,93,166,1);
    opacity:1;
  }
  .el-main .token-detail-content .el-row .el-col p img {
    width: 26px;
    height: 26px;
    vertical-align: middle;
    margin-left: 35px;
    cursor: pointer;
  }
</style>




